
.App {
    background-color: rgb(235, 235, 235);
    display: flex;
    flex-direction: column;
    justify-content: center;
    /* background-image: url('https://i.pinimg.com/736x/54/f6/fe/54f6fe85a42a39b6e57d2008cf18964f.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    background-blend-mode:soft-light; */
    /* height: 50rem; */
    width: 100vw;
    height: 100vh;
  }
  
  .Title {
      text-transform: uppercase;
      text-align: center;
      background-image: linear-gradient(
       -225deg,
       #231557 0%,
       #44107a 29%,
       #ff1361 67%,
       #fff800 100%
     );
     background-size: auto auto;
     background-clip: border-box;
     background-size: 200% auto;
     color: #fff;
     background-clip: text;
     -webkit-background-clip: text;
     -webkit-text-fill-color: transparent;
     animation: textclip 2s linear infinite;
     display: inline-block;
        font-size: 50px;      
   }
   
   @keyframes textclip {
     to {
       background-position: 200% center;
     }
  }
  
  .SearchBar-fields {
    display: flex;
    justify-content: center;
    margin-bottom: 2.88rem;
  }
  
  .SearchBar-fields input {
    width: 21rem;
    padding: .66rem 1rem;
    margin-right: 2.22rem;
    border: 1px solid #fff;
    border-radius: 4px;
    font-size: 1rem;
    font-weight: 500;
  }
  
  
  .SearchBar-submit {
    text-align: center;
  }
  
  .SearchBar-submit a {
    border-radius: 4px;
    padding: .72rem 1.7rem;
    background-color: #cca353;
    color: #ffffff;
    font-size: 1.5rem;
    font-weight: 600;
    transition: background-color .5s;
  }
  
  .SearchBar-submit a:hover {
    cursor: pointer;
    background-color: #a7874b;
  }

  
  
  @media only screen and (max-width: 560px) {
    .SearchBar {
      background-image: url('./background_search_mobile.jpg');
    }
  
    .SearchBar-sort-options ul {
      margin-left: 2rem;
      margin-right: 2rem;
    }
  
    .SearchBar-fields {
      flex-direction: column;
      align-items: center;
    }
  
    .SearchBar-fields input {
      margin-right: 0;
      margin-bottom: .86rem;
    }
  }

.table {
      color: #444;
      text-shadow: 0 1px 0 #fff;
    }
    
    strong {
      font-weight: bold; 
    }
    
    em {
      font-style: italic; 
    }
    
    table {
      background: #f5f5f5;
      border-collapse: separate;
      box-shadow: inset 0 1px 0 #fff;
      font-size: x-large;
      line-height: 24px;
      margin: 30px auto;
      text-align: center;
      width: 50%;
      /* width: 800px; */
      /* width: 1200px; */
      /* height: 600px; */
    } 
    
    th {
      background: url(https://jackrugile.com/images/misc/noise-diagonal.png), linear-gradient(#777, #444);
      border-left: 1px solid #555;
      border-right: 1px solid #777;
      border-top: 1px solid #555;
      border-bottom: 1px solid #333;
      box-shadow: inset 0 1px 0 #999;
      color: #fff;
      font-weight: bold;
      padding: 10px 15px;
      position: relative;
      text-shadow: 0 1px 0 #000;  
    }
    
    th:after {
      background: linear-gradient(rgba(255,255,255,0), rgba(255,255,255,.08));
      content: '';
      display: block;
      height: 25%;
      left: 0;
      margin: 1px 0 0 0;
      position: absolute;
      top: 25%;
      width: 100%;
    }
    
    th:first-child {
      border-left: 1px solid #777;  
      box-shadow: inset 1px 1px 0 #999;
    }
    
    th:last-child {
      box-shadow: inset -1px 1px 0 #999;
    }
    
    td {
      border-right: 1px solid #fff;
      border-left: 1px solid #e8e8e8;
      border-top: 1px solid #fff;
      border-bottom: 1px solid #e8e8e8;
      padding: 10px 15px;
      position: relative;
      transition: all 300ms;
    }
    
    td:first-child {
      box-shadow: inset 1px 0 0 #fff;
    } 
    
    td:last-child {
      border-right: 1px solid #e8e8e8;
      box-shadow: inset -1px 0 0 #fff;
    } 
    
    tr {
      background: url(https://jackrugile.com/images/misc/noise-diagonal.png); 
    }
    
    tr:nth-child(odd) td {
      background: #f1f1f1 url(https://jackrugile.com/images/misc/noise-diagonal.png); 
    }
    
    tr:last-of-type td {
      box-shadow: inset 0 -1px 0 #fff; 
    }
    
    tr:last-of-type td:first-child {
      box-shadow: inset 1px -1px 0 #fff;
    } 
    
    tr:last-of-type td:last-child {
      box-shadow: inset -1px -1px 0 #fff;
    } 
    
    tbody:hover td {
      color: transparent;
      text-shadow: 0 0 3px #aaa;
    }
    
    tbody:hover tr:hover td {
      color: #444;
      text-shadow: 0 1px 0 #fff;
    }